@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap);
*{
  margin: 0;
  padding: 0;
  border: 0;
  box-sizing: border-box;
}

body{
  font-family: 'Roboto', sans-serif;
}

.container{
  max-width: 800px;
  margin: 0 auto;
}

h1, h3{
  color: white;
}

#informacion{
  display: flex;
  flex-direction: row;
}

p{
  text-align: justify;
}

@media screen and (max-width: 500px) {
  body{
    font-family: 'Roboto', sans-serif;
    display: flex;
    flex-wrap: wrap;
  }

  .container{
    max-width: 500px;
  }

  #informacion{
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    align-content: center;
  }
}


header{
    background-image: url(data:image/png;base64,);
    background-repeat: no-repeat;
    display: flex;
    padding: 40px;
}
.foto{
    flex-basis: 20%;
    text-align: center;
}

.foto img{
    max-width: 170px;
    border-radius: 50%;
    border: 7px solid #FFFFFF;
}

.texto{
    flex-basis: 180%;
}

.texto h3{
    font-weight: 150;
    color: black;
}

.nombre{
    color: #e3610b;
}

@media screen and (max-width: 500px) {
    header{
        background-image: url(data:image/png;base64,);
        display: inline-block;
    }

    .ah1{
        color: black;
    }

    .foto{
        align-content: center;
    }
  }
.contacto{
    border-right: 3px solid #EAEAEA;
    padding: 10px 30px;
    flex-basis: 50%;
  }
.info{
    margin-bottom: 5px;
}

.info i{
    background-color: #e37e0b;
    padding: 10px;
    border-radius: 50%;
    margin-right: 10px;
    font-size: 20px;
}

.ref i{
    font-size: 2em;
    color: #574127;
    padding: 10px;
}

.refnombre{
    font-weight: 500;
}

.refcargo{
    padding-left: 55px;
}

.enlaces img{
    display:block;

margin-left: auto;

margin-right: auto;
    max-width: 150px;
}

h2{
    text-align: center;
}

.he5{
    text-align: center;
}

hr{
    border-bottom: 4px solid #574127;
    width: 50%;
    margin: 5px auto;
}

@media screen and (max-width: 500px) {
    .contacto{
        border-right: 3px solid #EAEAEA;
        margin: 5px;
      }
  }
.acerca{
    flex-basis: 50%;
  }

.habilidades{
  margin-left: 5px;
}

.infolab{
    padding: 10px ;
}


.titlab{
    background-color: #574127;
    border-top-right-radius: 20px;
    border-bottom-right-radius: 20px;
    padding: 5px 20px;
    color: white;
    margin: 10px 0;
}

.año{
  color: black;
  font-size: 13px;
  padding-left: 12px;
}

.estudio p{
    padding-left: 30px;
    margin: 10px 0;
}

.estudio h4{
  margin-left: 0.8rem;
}

progress{
  margin-left: 60px;
}

progress[value] {
    -webkit-appearance: none;
    appearance: none;
    width: 200px;
    height: 15px;
  }
  progress[value]::-webkit-progress-bar {
    background-color: #cccccc;
    border-radius: 4px;
  }
  progress[value]::-webkit-progress-value {
    background-image: -webkit-linear-gradient(-45deg, transparent 33%, rgba(0, 0, 0, .2) 33%, rgba(0, 0, 0, .2) 66%, transparent 66%), -webkit-linear-gradient(top, rgba(255, 255, 255, .25), rgba(0, 0, 0, .25)), -webkit-linear-gradient(left, #ffae00, #995c00);
    border-radius: 4px;
    background-size: 20px 15px, 100% 100%, 100% 100%;
  }

@media screen and (max-width: 500px) {
  .acerca{
    flex-direction: column;
  }
}
footer{
    background-image: url(data:image/png;base64,);
    min-height: 100px;
}
